  color: #fff;
  text-decoration: none;
}

.module{
  margin-left: 50px;
  width:80vw;
  height:100vh;
  padding-top: 25px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box
}



.containero {
    background-color: rgb(37, 44, 44);
    display: block;
    margin :  auto;
    color: #ddd;
    border-radius: 10px;
    padding: 20px;
    font-family: 'Montserrat', sans-serif;
    max-width: 700px
}

.container0>p {
    font-size: 32px
}

.question {
    width: 75%
}

.options {
    position: relative;
    padding-left: 40px
}

#options label {
    display: block;
    margin-bottom: 15px;
    font-size: 14px;
    cursor: pointer
}

.options input {
    opacity: 0
}

.checkmark {
    position: absolute;
    top: -1px;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #555;
    border: 1px solid #ddd;
    border-radius: 50%
}

.options input:checked~.checkmark:after {
    display: block
}

.options .checkmark:after {
    content: "";
    width: 10px;
    height: 10px;
    display: block;
    background: white;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: 300ms ease-in-out 0s
}

.options input[type="radio"]:checked~.checkmark {
    background: #21bf73;
    transition: 300ms ease-in-out 0s
}

.options input[type="radio"]:checked~.checkmark:after {
    transform: translate(-50%, -50%) scale(1)
}

.btn-primary {
    background-color: #555;
    color: #ddd;
    border: 1px solid #ddd
}

.btn-primary:hover {
    background-color: #21bf73;
    border: 1px solid #21bf73
}

.btn-success {
    padding: 5px 25px;
    background-color: #21bf73
}

.ppt-headings{
  font-weight: bold;
}

.image-container img{
  display: block;
  margin: auto;
  max-width: 90vw;
}

.image-container p{
  text-align: center;
  text-align: left;
}

.quiz-img-textfield{
  display: flex;
  justify-content: center;
  margin-top: -25px;
  height: 36px;
}

@media(max-width:576px) {
    .question {
        width: 100%;
        word-spacing: 2px
    }
    .image-container{
      display: block;
      margin: auto;
      max-width: 80vw;
    }
}

#slide-n {
    width: 96%;
}

* {
    box-sizing: border-box
}

.mySlides-n {
    display: none
}

.slideshow-container-n {
    width: 600px;
    position: relative;
    margin: -10px;
    align-items: right;
}

/* Next & previous buttons */
.prev-n {
    cursor: pointer;
    top: 50%;
    width: auto;
    padding: 16px;
    position: absolute;
    margin-right: -20px;
    margin-left: -300px;
    margin-top: -22px;
    color: white;
    background-color: red;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
}

.next-n {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    color: white;
    background-color: red;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
}

/* Position the "next button" to the right */
.next-n {
    right: 0;
    border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev-n:hover, .next-n:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

/* Caption text */
.text-n {
    color: #f2f2f2;
    font-size: 15px;
    padding: 8px 12px;
    position: absolute;
    bottom: 8px;
    width: 100%;
    text-align: center;
}

/* The dots/bullets/indicators */
.dot-n {
    cursor: pointer;
    height: 13px;
    width: 13px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

/* Fading animation */
.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 2s;
    animation-name: fade;
    animation-duration: 2s;
}

@keyframes fade {
    from {
        opacity: .4
    }

    to {
        opacity: 1
    }
}

/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
    .text {
        font-size: 11px
    }
}

